import React from 'react'
import { TextField, Button, Stack, Container } from "@mui/material"
import { useFormControl } from '@mui/material/FormControl';
const keys = {
  "1550": "Carina C",
  "Hugsmom": "Christine",
  "Canihaveatrilliondollars": "Carina H",
  "mewanthugs": "Tim"
}
function Login({ setUser }) {
  const [value, setValue] = React.useState("")
  return (
    <Container sx={{ maxWidth: "400px", mt: 2 }} >
      <Stack direction="row" spacing={2} alignItems="center">
        <TextField id="standard-basic" label="Key" variant="filled" value={value} onChange={(a) => setValue(a.target.value)} />
        <Button onClick={() => {
          if (!keys[value]) return setValue("");
          setUser(keys[value])
        }} variant="contained">Login</Button>
      </Stack>
    </Container>

  )
}

export default Login