import logo from './logo.svg';
import './App.css';
import Login from './Login'
import User from './User';

import React from 'react';





function App() {
  const [this_user, setUser] = React.useState("");

  if(!this_user && localStorage.user != undefined){
    setUser(localStorage.user)
  }

  if (!this_user) {
    return (
      <div className="App">
        <Login setUser={setUser}/>
      </div>
    );
  }
  localStorage.user = this_user
  return (
    <User this_user={this_user}/>
  )
}


export default App;
