import React, { useEffect } from 'react'

import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue, get, child, set } from "firebase/database";
import { Typography, Stack, Container, Paper, IconButton, ButtonGroup, Button } from '@mui/material';
import FavoriteRoundedIcon from '@mui/icons-material/FavoriteRounded';
import AddIcon from '@mui/icons-material/Add';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import { AlternateEmail } from '@mui/icons-material';

// TODO: Replace the following with your app's Firebase project configuration
// See: https://firebase.google.com/docs/web/learn-more#config-object
const firebaseConfig = {
    // ...
    // The value of `databaseURL` depends on the location of the database
    databaseURL: "https://hugs-ef8bf-default-rtdb.asia-southeast1.firebasedatabase.app/",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

const dbRef = ref(getDatabase());

function addHugs(to_data, source_user_data) {
    const hugsRef = child(dbRef, `users/${to_data.name}/received/${source_user_data.name}`);
    const creditsRef = child(dbRef, `users/${source_user_data.name}/credits/`);
    set(creditsRef, source_user_data.credits - 1)
    set(hugsRef, to_data.received[source_user_data.name] + 1)
}

function removeHugs(to_data, source_user_data) {
    const hugsRef = child(dbRef, `users/${to_data.name}/received/${source_user_data.name}`);
    const creditsRef = child(dbRef, `users/${source_user_data.name}/credits/`);
    set(hugsRef, to_data.received[source_user_data.name] - 1)
}

function User({ this_user }) {
    const [data, setData] = React.useState({})
    const [disabled, setDisabled] = React.useState(false)
    
    useEffect(() => {
        onValue(dbRef, (snapshot) => {
            const val = snapshot.val().users;
            setData(val)
        });
    }, [])
    if (data == {}) return (<div>User</div>)
    const me = data[this_user];
    if (!me) return (<div>User</div>)
    console.log(new Date().getTime(), me.last_login)
    if(new Date().getTime() - me.last_login >= 86400000){
        set(child(dbRef, `users/${this_user}`), {...me, credits: me.credits + 2, last_login: new Date().getTime()})
    };
    const other_users = Object.keys(data)
        .filter(user => user != this_user)
        .map(function (user) {
            return { ...data[user], name: user }})
    return (
        <Container sx={{ padding: 3, margin: 0 }}>
            <Stack spacing={2} direcion="column">
                <Stack direction="row" alignItems="center" spacing={1}>
                    <Typography variant="h4">Hugs</Typography>
                    <FavoriteRoundedIcon color="primary" />
                </Stack>

                <Paper sx={{ padding: 3 }} variant="outlined">
                    <Stack spacing={2}>
                        <Typography variant="h6">Received hugs: {Object.values(me.received).reduce((a, b) => a + b)}</Typography>
                        {Object.keys(me.received).map((user) => (
                            <Stack direction="row" spacing={1} alignItems="center">
                                <Typography>{me.received[user]}</Typography>
                                <Typography>{" "}from {user}</Typography>
                            </Stack>
                        ))}
                    </Stack>
                </Paper>
                <Paper sx={{ padding: 3, maxWidth: "sw" }} variant="outlined">
                    <Stack spacing={1.5}>
                        <Typography variant="h6">Hugs to grant: {me.credits}</Typography>
                        {other_users.map(user_data => (
                            <Stack direction="row"  alignItems="center" spacing={2}>
                                
                                <Typography>{user_data.received[this_user]} hugs for {user_data.name}</Typography> 
                                <ButtonGroup variant="outlined" aria-label="outlined primary button group">
                                    <Button size="small" disabled={me.credits == 0 || disabled} onClick={() => addHugs(user_data, {...me, name: this_user})}>
                                        <FavoriteRoundedIcon />
                                    </Button>
                                    <Button onClick={() => removeHugs(user_data, {...me, name: this_user})} size="small" color="secondary" disabled={user_data.received[this_user] == 0}><RemoveCircleOutlineIcon /></Button>
                                </ButtonGroup>   
                            </Stack>

                        ))}
                    </Stack>
                </Paper>
            </Stack>



        </Container>
    )



}

export default User